import React from "react";
import "./header.css";
import Typewriter from "typewriter-effect";
import baffle from "baffle";

const letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ&~#{[|`^@]}&'(-_)=!:;,?./$*%<>";

function Header() {
  
  React.useEffect(() => {
    const target = baffle('.BigTitle')
    target.set({
        characters: letters,
        speed: 50
    })
    target.start()
    target.reveal(1000, 1000)
})

  

  return (
    <>
  

      <div className="App">
        <header className="App-header" id="header">
          <p className="App-title">
            <code className="BigTitle" data-value="Albatross !!">
              Albatross !!
            </code>
          </p>
          <p className="App-subtitle">
            <Typewriter
              options={{
                strings: [
                  "NSI cross-team",
                  "Back-end : Cedric, Thomas",
                  "Front-end : Claire, Olivier",
                  "React, NodeJS, React-Native",
                ],
                autoStart: true,
                loop: true,
                TimeRanges: 2000,
              }}
            />
          </p>
          <div className="buttons">
              <a href="http://jdocopilot.me/html/cgus" className="button">About</a>
              <a href="http://jdocopilot.me/html/contact" className="button">Work</a>
          </div>
        </header>        
      </div>
    </>
  );
}

export default Header;
